body{
    margin: 0;
    padding: 0;
    background-color: #c4c4c4;
}
.dashboard{
    width: 100%;
    display: grid;
    grid-template-columns: 15% 80%;
    grid-column-gap: 30px;
    height: 100vh;
    background-color: #c4c4c4;
}
aside{
    width: 100%;
    background-color: #858282;
    display: grid;
    grid-template-rows: 0.5fr 1fr;
    grid-row-gap: 10px;
    place-content: center;
}
.profile{
    /*border: 1px solid red;*/
    width: 150px;
    color: aliceblue;
    text-align: center;
    padding-top: 20px;
}
.profile h2{
}
/*.profile p{
    margin: 0;
    padding: 0;
    font-size: 12px;
}*/
.avatar{
    width:100px;
    height: 100px;
    border-radius: 100px;
    background-color: #111111;
    margin: auto;
    position: relative;
    overflow: hidden;
}
.avatar img{
    width:100%;
    height: 100%;
    object-fit: cover;
}
.avatar:hover{
    cursor: pointer;
    background-blend-mode: color-burn;
}
.avatar span{
    background-color: rgba(17, 17, 17, 0.589);
    width: 100px;
    height: 100px;
    padding: 0px;
    position: absolute;
    top:0;
    left: 0;
    visibility: hidden;
}
.avatar span:hover{
    visibility: visible;
}
.nav{
    display: flex;
    flex-direction: column;
    /*border: 1px solid rgb(221, 115, 115);*/
    height: 90%;
    place-content: center;
}
.nav-content{
    display: flex;
    flex-direction: row;
    border-bottom: 2px solid #a3a1a1;
    padding: 10px;
    color: aliceblue;
    cursor: pointer;
}
.nav-content .r-icon{
    padding: 1px;
    font-size: 1.5em;
    background-color: transparent;
}
a{
    text-decoration: none;
    color: inherit;
}
.monitor{
    width:100%;
    /*border: 1px solid red;*/
    display: grid;
    grid-template-rows: 70% 30%;
    position: relative;
}
.map{
    background-color: #969494;
    width: 100%;
    height: 100%;
}
.search{
    display: flex;
    flex-direction: column;
    grid-row-gap: 20px;
    background-color: #222121;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 96.5%;
    height: 45%;
    position: absolute;
    bottom: 0;
    padding: 20px;
    color: aliceblue;
}
input.search-bar{
    width: 50%;
    margin: 0 auto;
    background-color: #d3d1d1;
    color: rgb(27, 27, 27);
    text-align: left;
    padding: 10px 10px 10px 30px;
    border-radius: 10px;
    outline: none;
    border: none;
}
.pre-rescues{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 80%;
    margin: 0 auto;
    padding: 10px 0;
}
.pre-res{
    display: flex;
    flex-direction: row;
    text-align: center;
}
.r-icon{
    width: 20px;
    height: 20px;
    background-color: #c4c4c4;
    padding: 5px;
    border-radius: 100px;
    margin: auto 5px auto 0;
    text-align: center;
}
.result{
    display: flex;
    flex-direction: column;
    grid-row-gap: 20px;
    background-color: #4d72c2;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 96.5%;
    height: 60%;
    position: absolute;
    bottom: 0;
    padding: 20px 20px 0 20px;
    color: aliceblue;
    visibility: hidden;
}/*
.result .search-bar{
    width: 40%;
    margin: 0 auto 0 10%;
    background-color: #ffffff85;
    color: rgb(208, 210, 211);
    text-align: center;
    padding: 10px;
    border-radius: 10px;
}*/
.result-info{
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 100%;
    justify-content: space-between;
}
.contact-info{
    width: 50%;
    height: 100%;
    background-color: rgb(87, 87, 88);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.contact-info-header{
    border-bottom: 1px solid #a09e9e;
    text-align: center;
    padding: 5px;
}
.rescuer{
    width: 70px;
    height: 70px;
    border-radius: 100px;
    background-color: #111111;
    margin: auto;
    margin-top: 20px;
    position: relative;
    text-align: center;
    color: aliceblue;
}
.rescuer img{
    width:100%;
    height: 100%;
    object-fit: cover;
}
.details{
    display: flex;
    flex-direction: row;
    height: 40px;
    justify-content: center;
}
.details span{
    width:40px;
    border-right: 1px solid white;
    text-align: center;
    align-self: center;
}
.details p{
    padding-left: 30px;
    align-self: center;
    text-align: center;
}
.more-result{
    width: 45%;
    height:70%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    overflow: hidden;
    align-self: flex-end;
}
.more-result:hover{
    overflow: scroll;
}
.toggle{
    background-color: #a09e9e;
    width:30px;
    height: 5px;
    margin: 0 auto;
    cursor: pointer;
}






pin {
    width: 30px;
    height: 30px;
    border-radius: 50% 50% 50% 0;
    background: #b15555;
    position: absolute;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    left: 50%;
    top: 50%;
    margin: -20px 0 0 -20px;
    -webkit-animation-name: bounce;
    -moz-animation-name: bounce;
    -o-animation-name: bounce;
    -ms-animation-name: bounce;
    animation-name: bounce;
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    -o-animation-fill-mode: both;
    -ms-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -moz-animation-duration: 1s;
    -o-animation-duration: 1s;
    -ms-animation-duration: 1s;
    animation-duration: 1s;
  }
  .pin:after {
    content: '';
    width: 14px;
    height: 14px;
    margin: 8px 0 0 8px;
    background: #302558;
    position: absolute;
    border-radius: 50%;
  }
  .pulse {
    background: rgba(0,0,0,0.2);
    border-radius: 50%;
    height: 14px;
    width: 14px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: 11px 0px 0px -12px;
    -webkit-transform: rotateX(55deg);
    -moz-transform: rotateX(55deg);
    -o-transform: rotateX(55deg);
    -ms-transform: rotateX(55deg);
    transform: rotateX(55deg);
    z-index: -2;
  }
  .pulse:after {
    content: "";
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: absolute;
    margin: -13px 0 0 -13px;
    -webkit-animation: pulsate 1s ease-out;
    -moz-animation: pulsate 1s ease-out;
    -o-animation: pulsate 1s ease-out;
    -ms-animation: pulsate 1s ease-out;
    animation: pulsate 1s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    -ms-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
    -webkit-box-shadow: 0 0 1px 2px #b15555;
    box-shadow: 0 0 1px 2px #b15555;
    -webkit-animation-delay: 1.1s;
    -moz-animation-delay: 1.1s;
    -o-animation-delay: 1.1s;
    -ms-animation-delay: 1.1s;
    animation-delay: 1.1s;
  }
  @-moz-keyframes pulsate {
    0% {
      -webkit-transform: scale(0.1, 0.1);
      -moz-transform: scale(0.1, 0.1);
      -o-transform: scale(0.1, 0.1);
      -ms-transform: scale(0.1, 0.1);
      transform: scale(0.1, 0.1);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
    50% {
      opacity: 1;
      -ms-filter: none;
      filter: none;
    }
    100% {
      -webkit-transform: scale(1.2, 1.2);
      -moz-transform: scale(1.2, 1.2);
      -o-transform: scale(1.2, 1.2);
      -ms-transform: scale(1.2, 1.2);
      transform: scale(1.2, 1.2);
      opacity: 0;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: alpha(opacity=0);
    }
  }
